<template>
  <div id="login">
  <div class="modal-mask">
    <div class="modal-wrapper">
      <div class="modal-container">
        <div class="main"></div>
        <div class="form">
          <h3 @click="showRegister"></h3>
          <transition name="slide">
            <!-- class,isShowRegistertrueshowclass -->
          <div :class="{show:isShowRegister}" class="register">
            <input type="text" placeholder="" v-model="register.username">
            <input type="password" placeholder="" v-model="register.password" @keyup.enter="onRegister">
            <p :class="{error:register.isError}">{{register.notice}}</p>
            <div @click="onRegister" class="button"></div>
          </div>
          </transition>
          <h3 @click="showLogin"></h3>
           <transition name="slide">
            <!-- class,isShowLogitrueshowclass -->
          <div class="login" :class="{show:isShowLogin}">       
            <input type="text" placeholder="" v-model="login.username">
            <input type="password" placeholder="" v-model="login.password" @keyup.enter="onLogin">
            <p :class="{error:login.isError}">{{login.notice}}</p>  
            <div class="button" @click="onLogin"></div>
          </div>


            </transition>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import Bus from '@/helpers/bus' //
import Auth from '@/apis/auth'
import {Message} from 'element-ui'
import {  mapActions, mapGetters} from 'vuex'


 Auth.getInfo()   //
 .then(data => {
 console.log(data)
 })
// request('/auth')
// .then(data => {
//     console.log(data)
// })

export default {


  data () {
    return {
      isShowLogin:true,
      isShowRegister:false,
      login:{       
        username:'',   //input
        password:'',
        notice:'',   //
        isError:false
      },
      register:{  
        username:'',  //input
        password:'',
        notice:'',
        isError:false
      }
     
    }
  },

 

  methods:{
   ...mapActions({
     loginuser:'login',
     registeruser:'register'
   }),
  
   

      // ...mapActions({
      //   loginUser: 'login',
      //   registerUser: 'register'
      //   }),

    showLogin() {
      this.isShowLogin = true,
      this.isShowRegister = false
    },
    showRegister() {
      this.isShowLogin = false
      this.isShowRegister = true
    },
    onRegister() {                  //test
      if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {  //iserrortrue
        console.log(this.register.username)///^[\w\u4e00-\u9fa5]{3,15}$/.tset('123')
        this.register.isError = true
        this.register.notice = '3~15'
        return
      }
      if(!/^.{6,16}$/.test(this.register.password)) {
        console.log(this.register.password) ///^.{6,16}$/.tset('123456')
        this.register.isError = true
        this.register.notice = '6~16'
        return
      }
      
      // console.log(`start register...,username:${this.register.username},password:${this.register.password}`) 
      
     this.registeruser({
              username:this.register.username,  //
              password:this.register.password
              }).then(() => {
                 this.register.isError = false     //   
                this.register.notice = ''
               // Bus.$emit('userInfo',{username:this.login.username})//$emit
                this.$router.push({path:'notebooks'})   //
               
              }).catch(data => {
                this.register.isError = true       //
                this.register.notice = data.msg   //
              })

    },
    onLogin() {
       if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {  //
        this.login.isError = true
        this.login.notice = '3~15'
        return
      }
      if(!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true
      
        this.login.notice = '6~16'
        return
      }
      
     // console.log(`start login..., username: ${this.login.username} , password: ${this.login.password}`)
 
     this.loginuser({
        username:this.login.username,    //
        password:this.login.password
        }).then(() => {  //
          this.login.isError = false    //
          this.login.notice = ''
          this.$message({
        type:'success',
         message:''
        
       })
       //  Bus.$emit('userInfo',{username:this.login.username})  //userInfoavtar
         // console.log('start redirect ..')
          this.$router.push({ path:'notebooks' }) //
        }).catch(data => {
          this.login.isError = true   //
          this.login.notice = data.msg //
        })

    },
  
  }
}
</script>

<style lang="less" scoped>   
.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0,0,.7);
  display: table;   //
  transition: opacity .3 ease; //
}
.modal-wrapper {
  display: table-cell; //
  vertical-align: middle;  //
}
.modal-container {
  width: 800px;
  height: 500px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0,0,0,.33);
  transition: all .3 ease;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}
.main {
  flex: 1;
  background: #36bc64 url(//cloud.hunger-valley.com/17-12-13/38476998.jpg-middle) center center no-repeat;  //
  background-size: contain;
}
.form {
  width: 270px;
  border-left: 1px solid #ccc;
  overflow: hidden;  //
}
h3 {
  padding: 10px 20px;
  margin-top: -1px;
  font-weight: normal;  //
  font-size: 16px;
  border-top: 1px solid #eee;
  cursor: pointer;
  &:nth-of-type(2){
    border-bottom: 1px solid #eee;
  }
}
.button {
  background-color: #2bb964;
  height: 36px;
  line-height: 36px;
  text-align: center;
  font-weight: bold;//
  color: #fff;
  border-radius: 4px;
  margin-top: 18px;
  cursor: pointer;
}


.login,.register {
padding: 0px 20px;
border-top: 1px solid #eee;
height: 0;
overflow: hidden;
transition: height .4s;
&.show {
  height: 193px;
}


}
input {
  display: block;
  width: 100%;
  height: 35px;
  line-height: 35px;
  padding: 0 6px;
  border-radius: 4px;
  border: 1px solid #ccc;
  outline: none;  //
  font-size: 14px;
  margin-top: 10px;
}
input:focus {   //
  border: 3px solid #9dcaf8;
}
p {
  font-size: 12px;
  margin-top: 10px;
  color: #444;
}
.error {
  color: red;
}
.login {
  border-top: 0;
}
</style>


