<template>
<div class="detail" id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"><i class="iconfont icon-plus"></i> </a>
    </header>
    <main>
      <div class="layout">
        <h3>({{notebooks.length}})</h3>
        <div class="book-list">
      <router-link v-for="notebook in notebooks" :to="`/note?notebookId=${notebook.id}`" class="notebook"  :key="notebook.id"> 
            <div>
              <span class="iconfont icon-notebook">{{notebook.title}}   {{notebook.noteCounts}}</span> 
            
                 <!-- stopprevent -->
              <span class="action" @click.stop.prevent="onEdit(notebook)"></span>  
              <span class="action" @click.stop.prevent="onDelete(notebook)"></span>  
              <span class="date">{{notebook.createdAtFriendly}}</span>              
            </div>
           </router-link>           
        </div>       
      </div>

    </main>

  </div>
</template>

<script>

import Auth from '@/apis/auth'
import Notebooks from '@/apis/notebooks'
import { friendlyDate } from '@/helpers/util'
import { mapState, mapActions, mapGetters} from 'vuex'

export default {
 
  data () {
    return {
      // notebooks:[] //
      
    }
  },

  created() {   //
  // this.checkLogin({path:'/login'})  //login
   this.getNotebooks()
    // Auth.getInfo()   //
    // .then(res => {
    //   if(!res.isLogin) {   //
    //     this.$router.push({path:'/login'})
    //   }
      
    // })
    // Notebooks.getAll()   //
    // .then(res => {
      // const a  = res.data.reverse()
      // this.notebooks =  a
      //console.log(this.notebooks)//
    // })
     //this.$store.dispatch('getNotebooks')  //actions,
  
  }, 
  computed:{
    ...mapGetters(['notebooks'])
  },
    
  methods:{
   ...mapActions([  //actions
    'getNotebooks',
    'addNotebook',
    'updateNotebook',
    'deleteNotebook',
    'checkLogin'
   ]),


    onCreate() {
      this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^.{1,30}$/,
          inputErrorMessage: '30'
        }).then(({ value }) => {
          this.addNotebook({title:value})
       // return Notebooks.addNotebook({title:value})

      })
    //  .then(res => {
        
      //   res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt)
      //   this.notebooks.unshift(res.data) //
      // this.$message({
      //       type: 'success',  //
      //       message:res.msg
      //     })
      // })
        // .catch((res) => {
        //  this.$message({
          //  type:'error',
          //  messag:res.msg

        //  })       
        // })
    },



    onEdit(notebook) {
      let title =''
         this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^.{1,30}$/,
          inputValue:notebook.title,   //
          inputErrorMessage: '30'
        }).then(({ value }) => {
          title = value
        this.updateNotebook({notebookId:notebook.id, title:title})
       // return Notebooks.updateNotebook(notebook.id, {title})

        })
        // .then(res => {
        //  notebook.title = title
        // res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt)
        // this.notebooks.unshift(res.data) //
      // this.$message({
      //       type: 'success',
      //       message:res.msg
      //     })
      // })
      //   .catch((res) => {
      //    this.$message({
          //  type:'error',
          //  messag:res.msg
        //    type: 'info',
        //     message: ''
        //  })       
        // })





    //  let title = window.prompt('',notebook.title) //
    //  Notebooks.updateNotebook(notebook.id,{ title })
    //  .then(res => {
    //    console.log(res)
    //    notebook.title = title
    //    alert(res.msg)
    //  })
    },



    onDelete(notebook) {  //
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteNotebook({notebookId:notebook.id})
        })
        
      //   .then(res => {
      // this.notebooks.splice(this.notebooks.indexOf(notebook),1)
      // //this.notebooks.splice(this.notebooks.indexOf(notebook),1)
      // this.$message({
      //   type:'success',
      //   message:res.msg
      //     // message:''
      // })
      //   }).catch(res => {
      //     this.$message({
      //   // type:'error',
      //   // messag:res.msg
      //   type: 'info',
      //       message: ''
      //   })
      // })





    //  let isConfirm = window.confirm('')
    //  if(isConfirm) {
    //    Notebooks.deleteNotebook(notebook.id)
    //    .then(res => {
    //      console.log(res)
    //      this.notebooks.splice(this.notebooks.indexOf(notebook),1)
    //      alert(res.msg)
    //    })
    //  }
    }
  }
}
</script>

<style lang="less" scoped>
@import  url(../assets/css/notebook-list.less);
</style>


