<template>
  <div id="note" class="detail">
    <!--     -->
    <note-sidebar @update:notes="(val) => (notes = val)"></note-sidebar>
    <div class="note-detail">
      <!-- curnote.idcueNote -->
       <div class="note-empty" v-show="!curBook.id"></div>
    <div class="note-empty" v-show="!curNote.id"></div>
      <div class="note-detail-ct" v-show="curNote.id">
        <div class="note-bar">
          <span>: {{ curNote.createdAtFriendly }} </span>
          <span>:{{ curNote.updatedAtFriendly }} </span>
          <span>{{ statusText }}</span>
          <span class="iconfont icon-delete" @click="ondeleteNote"></span>
          <span
            class="iconfont icon-fullscreen"
            @click="isShowPreview = !isShowPreview"
          ></span>
        </div>

        <div class="note-title">
          <!--v-model valuetitlevalue   @keydown="statusText='....'"    -->
          <input
            type="text"
            v-model:value="curNote.title"
            @input="onupdateNote"
            @keydown="statusText = '....'"
            placeholder=""
          />
        </div>
        <div class="editor">
          <!-- valuecontentvalue -->
          <textarea
            v-show="!isShowPreview"
            v-model:value="curNote.content" 
            @input="onupdateNote"
            @keydown="statusText = '....'"
            placeholder=" markdown "
          ></textarea>
          <div
            class="preview markdown-body"
            v-html="previewContent"
            v-show="isShowPreview"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from "@/apis/auth";
import NoteSidebar from "./NoteSidebar.vue";
import Bus from "@/helpers/bus";
import _ from "lodash"; //
import Notes from "@/apis/notes"; //
import MarkdownIt from "markdown-it"; //markdownhtml
let md = new MarkdownIt();

import Trash from '@/apis/trash'
window.Trash = Trash  //
//Trash.getAll().then(res => {console.log(res)})

import { mapState, mapActions, mapGetters, mapMutations } from "vuex";

export default {
  components: { NoteSidebar },

  data() {
    return {
      //  curNote:{ },  //curNotenotes
      // notes:[],  //sildebar
      statusText: "", //
      isShowPreview: false,
    };
  },

  created() {
    //console.log('curNote', this.curNote)
   // console.log('curBook', this.curBook)
    this.checkLogin({path:'/login'})
    // Auth.getInfo() //
    //   .then((res) => {
    //     if (!res.isLogin) {
    //       //isLoginfalsepush,replace
    //       this.$router.push({ path: "/login" });
    //     }
    //   });

    // Bus.$once('update:notes',val => {  //oncee
    //   this.curNote = val.find(note => note.id == this.$route.query.noteId) || {}  //sildbar
    // })
  },

  computed: {
    ...mapGetters(["notes","curNote","curBook"]),

    previewContent() {
      
      return md.render(this.curNote.content || ""); //markdownhtml,
    },
  },

  beforeRouteUpdate(to, from, next) {
    this.setCurNote({curNoteId:to.query.noteId})
       next();
  },
    //
    //console.log("beforeRouteUpdate");
    //console.log(to, from);

    //console.log(this.curNote)

    // this.curNote = this.notes.find( note => note.id == to.query.noteId)
 

  methods: {
 ...mapMutations([
  'setCurNote',
  

 ]),

  ...mapActions([
   'updateNote',
   'deleteNote',
   'checkLogin'   

  ]),


    //@input="updateNote" 
    onupdateNote: _.debounce(function () {  //
      if(!this.curNote.id) return
      this.updateNote(
        { noteId: this.curNote.id ,
        title: this.curNote.title, content: this.curNote.content }
      ) //
        .then((data) => {
          this.statusText = ""; //
        })
        .catch((data) => {
          this.statusText = ""; //
          console.log("error", data);
        });
    }, 300), //300,

    ondeleteNote() {
      //
      this.deleteNote({ noteId: this.curNote.id }).then((data) => {
      // this.$message.success(data.msg);
       // this.notes.splice(this.notes.indexOf(this.curNote), 1); //notesildebar
        this.$router.replace({ path: "/note" }); //note
      })
    }
  }
};
</script>

<style lang="less" scoped>
#note {
  display: flex;
  align-items: stretch; //:
  background-color: #fff;
  flex: 1;
}

.note-detail {
  flex: 1;
  display: flex;
  flex-direction: column; //

  .note-detail-ct {
    height: 100%;
  }

  .note-empty {
    font-size: 50px;
    color: #ccc;
    text-align: center;
    margin-top: 100px;
  }

  .note-bar {
    padding: 10px 30px;
    border-bottom: 1px solid #eee;
    font-weight: bold;
    margin-right: 5px;
    &:after {
      content: "";
      display: block;

      clear: both;
    }
    span {
      font-size: 12px;
      color: #999;
      margin-right: 4px;
    }
    .iconfont {
      float: right;
      margin-left: 10px;
      font-size: 18px;
      cursor: pointer;
    }
  }

  .note-title {
    input,
    span {
      display: inline-block;
      width: 100%;
      border: none;
      outline: none;
      font-size: 18px;
      padding: 10px 20px;
    }
  }

  .editor {
    height: ~"calc(100% - 70px)";
    position: relative;

    .vue-codemirror {
      height: ~"calc(100% - 25px)";

      .CodeMirror {
        height: 100%;
        padding: 10px;
      }
    }
  }
  textarea,
  .preview {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 20px;
  }

  textarea {
    border: none;
    resize: none;
    outline: none;
    font-size: 14px;
    font-family: "Monaco", courier, monospace;
  }

  code {
    color: #f66;
  }
}
</style>


